@keyframes slidedown {

  0% {
    height: 0;
  }

  100% {
    height: 200px;
  }
}

.nav-container {
  min-width: 320px;
  box-sizing: border-box;
  width: 100%;
  background-color: #fff;
  border-top: 3px solid #f48024;
  border-bottom: 1px solid #ccc;
  height: 80px;
  display: flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  position: fixed;
  top: 0;
  z-index: 1000;

  .nav-chat-message-box {
    height: 300px;
    width: 40%;
    background-color: black;
    color: #fff;
    opacity: .7;
    z-index: 1001;
    position: fixed;
    right: 0;
    top: 300px;
    overflow-y: auto;
    overscroll-behavior: contain;
    

    .nav-chat-message-title {
      text-align: center;
      font-size: 24px;
      color: red;
    }

    .nav-chat-message-container {

      border-bottom: white 1px solid;
      margin-bottom: 2px;

      .nav-chat-list-name {
        font-size: 14px;
        color: rgb(8, 247, 64);

        .nav-chat-list-username {
          font-size: 18px;
          color: hotpink;
          margin: 0;
          cursor: pointer;
        }
        
      }

      .nav-chat-list-message {
        font-size: 20px;
      }

      .nav-chat-list-time {
        font-size: 14px;
        opacity: .6;
      }
    }
  }

  .nav-panel {
    position: absolute;
    background-color: black;
    padding: 10px 20px;
    color: #fff;
    top: 200px;
    left: 50%;
    transform: translateX(-50%);
    // width: 200px;
    font-size: 30px;
    opacity: .8;
    border-radius: 20px;
  }

  .nav-welcome {
    margin-left: 20px;
    font-size: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;

    .nav-username {
      margin: 0;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }

    .nav-user-signature {
      font-size: 13px;
      color: black;
      opacity: .5;
      max-width: 300px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      cursor: pointer;
    }
  }

  .nav-logo {
    flex-shrink: none;
    width: 60px;
    height: 60px;
    margin-left: 30px;
  }

  .nav-main-text {
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    font-size: 30px;
    margin-left: 20px;
    flex-shrink: none;

  }

  .nav-right-phone {
    flex-shrink: none;
    height: 80px;
    width: 80px;
    margin-left: auto;
    margin-right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    .nav-right-phone-barbox {
      flex-shrink: none;
      padding: 5px 0;
      height: 45px;
      width: 60px;
      border: 2px #ccc solid;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: center;

      .nav-right-phone-line {
        background-color: #ccc;
        width: 40px;
        height: 4px;
      }
    }

    .nav-right-phone-more-pannel {
      width: 100px;
      position: absolute;
      top: 80px;
      animation: slidedown .2s ease-in-out forwards;
      display: flex;
      flex-direction: column;
      background-color: #fff;
      border: 2px #f48024 solid;

      .nav-right-phone-more-pannel-line {
        width: 100px;
        border-bottom: 2px solid #ccc;
        height: 25%;
        // height: 40px;
        text-align: center;
        line-height: 50px;
        box-sizing: border-box;
      }
    }
  }

  .nav-right {
    margin-left: auto;
    margin-right: 20px;
    display: flex;
    flex-direction: row;
    height: 80px;
    align-items: center;
    width: 450px;
    justify-content: space-around;

    .nav-right-button {
      cursor: pointer;
      width: 100px;
      height: 60px;
      border: 2px red solid;
      border-radius: 3px;
      text-align: center;
      line-height: 60px;
      font-size: 18px;
    }
  }

  .nav-right-goback {
    margin-left: auto;
    margin-right: 20px;
    display: flex;
    flex-direction: row;
    height: 80px;
    align-items: center;
    // width: 120px;
    justify-content: space-around;

    .nav-right-button {
      cursor: pointer;
      width: 130px;
      height: 60px;
      border: 2px red solid;
      border-radius: 3px;
      text-align: center;
      line-height: 60px;
      font-size: 18px;
      margin: 0 10px;
    }
  }
}
